'use strict';

var multihash = require('multihashes'); // TODO: list only safe hashes https://github.com/ipfs/go-verifcid


module.exports = function hashes() {
  return Object.keys(multihash.names).map(function (name) {
    return {
      name: name,
      code: multihash.names[name]
    };
  });
};